.figure-table {
  padding: 0;
  margin: 0 auto;
  margin-top: 2rem;
  margin-bottom: 1rem;
  position: relative;
}
.figure-table .table-tool-bar ul {
  margin: 0;
  padding: 0;
  display: flex;
  list-style-type: none;
  justify-content: space-between;
  margin-bottom: 0.5rem;
}
.figure-table .table-tool-bar ul li {
  cursor: pointer;
  padding: 0.3rem;
  border: 1px solid #89e99c;
  border-radius: 3px;
  min-width: 4rem;
  text-align: center;
}
.figure-table .table-tool-plus {
  position: absolute;
  width: 1.5rem;
  height: 1.5rem;
  margin-top: -0.75rem;
  margin-left: -0.75rem;
  border-radius: 0.75rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.figure-table .table-tool-plus:hover {
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.015);
  cursor: pointer;
  background-color: #405261;
}
.figure-table .table-tool-plus:hover span {
  color: white;
}
.figure-table .table-tool-plus span {
  font-size: 1.5rem;
}
.figure-table .table-tool-menu-row {
  position: absolute;
  width: 1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.figure-table .table-tool-menu-row:hover {
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.015);
  cursor: pointer;
  background-color: #405261;
}
.figure-table .table-tool-menu-row:hover span {
  color: white;
}
.figure-table .table-tool-menu-row span {
  font-size: 1.5rem;
}
.figure-table .table-tool-menu-col {
  position: absolute;
  height: 2.3rem;
  margin-top: -2.3rem;
}
.figure-table .table-tool-menu-col ul {
  margin: 0;
  padding: 0;
  display: flex;
  list-style-type: none;
  justify-content: space-between;
}
.figure-table .table-tool-menu-col ul li {
  flex: 1;
  text-align: center;
}
.figure-table .table-tool-menu-col ul li:hover {
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.015);
  cursor: pointer;
  background-color: #405261;
}
.figure-table .table-tool-menu-col ul li:hover span {
  color: white;
}
.figure-table table {
  border-collapse: collapse;
  width: 100%;
  position: relative;
}
.figure-table table thead tr {
  background: var(--table-head-background-color, #f5f5f5);
  font-weight: bold;
}
.figure-table table td {
  border: 1px solid var(--table-border-color, #e5e5e5);
  padding: 0.5em;
  position: relative;
}
.figure-table table td.table-cell-align-center {
  text-align: center;
}
.figure-table table td.table-cell-align-left {
  text-align: left;
}
.figure-table table td.table-cell-align-right {
  text-align: right;
}
